/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
  max-width: 100vw;
}
/*body {
  font-family: "Montserrat", sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
}*/

/* Media queries para pantallas pequeñas */
@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }

  .container {
    grid-template-columns: 1fr;
    padding: 10px;
  }

  .box {
    padding: 12px;
  }
}

div#main {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centra el contenido horizontalmente */
  width: 100%;
  /*width: 100vw;*/
}

section {
  position: relative;
  width: 100%;
  padding: 20px;
  /*width: 100vw;*/
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  left: 50%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100vmax;
  z-index: -1;
}

