/* Ajustes para encabezados */
h1 {
  font-size: 4rem; /* Reduce el tamaño del encabezado en pantallas pequeñas */
  line-height: 1.2;
  text-align: center;
}

h2 {
  font-size: 1.8rem;
  text-align: center;
  padding-top: 0.5rem;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  margin: 20px auto;
  max-width: 100%;
}

.box {
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  padding: 16px;
  text-align: center;
  /*display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;*/
  
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

a.hover-link {
  color: white;
  text-decoration: none; /* Quita el subrayado */
}

a.hover-link:hover {
  color: #FFD700; /* Cambia a color dorado al pasar el mouse */
  text-decoration: underline; /* Subraya el texto al pasar el mouse */
}