@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  width: 100%;
  max-width: 100vw;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

@media (width <= 768px) {
  h1 {
    font-size: 2.5rem;
  }

  .container {
    grid-template-columns: 1fr;
    padding: 10px;
  }

  .box {
    padding: 12px;
  }
}

div#main {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

section {
  width: 100%;
  padding: 20px;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  object-fit: cover;
  pointer-events: none;
  z-index: -1;
  width: 100vmax;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  text-align: center;
  font-size: 4rem;
  line-height: 1.2;
}

h2 {
  text-align: center;
  padding-top: .5rem;
  font-size: 1.8rem;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  max-width: 100%;
  margin: 20px auto;
  display: grid;
}

.box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 16px;
}

img.socialIcon {
  width: 30px;
  height: 30px;
}

a.hover-link {
  color: #fff;
  text-decoration: none;
}

a.hover-link:hover {
  color: gold;
  text-decoration: underline;
}
